@import url(https://fonts.googleapis.com/css?family=Nunito:400|Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Nunito', sans-serif;padding-top:20px}.header-Title{font-family:'Nunito', sans-serif;font-weight:bolder;font-size:1.5em;color:#1C110A}.mini-headline{color:#a19e9e}.last-dest-col{height:160px}.card{border:none}.last-dest-card{height:160px}.last-dest-img{object-fit:cover;width:100%;height:100%;border-radius:1em}.card-title{margin-bottom:0}.beauti-citi-col{display:flex;flex-wrap:nowrap;overflow-x:auto;margin-bottom:100px}.beauti-citi-div{width:150px;height:170px;background-color:#cdcdcd;border-radius:1em;flex:0 0 auto;margin-right:15px;margin-bottom:20px}#style-3::-webkit-scrollbar-track{background-color:#F5F5F5}#style-3::-webkit-scrollbar{margin-top:10px;width:6px;height:5px;background-color:#cdcdcd}#style-3::-webkit-scrollbar-thumb{background-color:#cdcdcd}.footer{position:fixed;bottom:0;width:100%;height:50px;padding-left:6px;padding-right:6px;line-height:50px;background-color:white;left:0;border-top-left-radius:1.5em;border-top-right-radius:1.5em;box-shadow:0px 1px 24px -17px rgba(0,0,0,0.45)}.navi-icon{font-size:1.3em}.a-menu.active{color:#51CFD6}

