@import url('https://fonts.googleapis.com/css?family=Nunito:400|Abril+Fatface&display=swap');

$Darkbrown: #1C110A;
$Red: #BF0603;
$Pearl: #EDE4C7;
$Orange: #E5AC39;
$Turquoise: #51CFD6;
$gray: #a19e9e;
$lightgray: #cdcdcd;

body {
  font-family: 'Nunito', sans-serif;
  padding-top: 20px;
}


.header-Title {
  font-family: 'Nunito', sans-serif;
  font-weight: bolder;

  font-size: 1.5em;

  color: $Darkbrown;
}

.mini-headline {
  color: $gray;
}

.last-dest-col {
  height: 160px;
}

.card {
  border: none;
}

.last-dest-card{
  height: 160px;
}

.last-dest-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 1em;
}

.card-title{
  margin-bottom: 0;
 
}


.beauti-citi-col{
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  margin-bottom: 100px;
}

.beauti-citi-div{
  width: 150px;
  height: 170px;
  background-color: $lightgray;
  border-radius: 1em;
  flex: 0 0 auto;
  margin-right: 15px;
  margin-bottom: 20px;
}




#style-3::-webkit-scrollbar-track {
	background-color: #F5F5F5;
}

#style-3::-webkit-scrollbar
{
  margin-top: 10px;
  width: 6px;
  height: 5px;
	background-color: #cdcdcd;
}

#style-3::-webkit-scrollbar-thumb
{
	background-color: $lightgray;
}








.footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  padding-left:6px;
  padding-right:6px;
  line-height: 50px;
  background-color: white;
  left:0;
 border-top-left-radius: 1.5em;
 border-top-right-radius: 1.5em;
 -webkit-box-shadow: 0px 1px 24px -17px rgba(0,0,0,0.45);
-moz-box-shadow: 0px 1px 24px -17px rgba(0,0,0,0.45);
box-shadow: 0px 1px 24px -17px rgba(0,0,0,0.45);

}

.navi-icon{
  font-size: 1.3em;

}

.a-menu.active{
  color: $Turquoise;
}